.loading {
	@extend %c_ab_full;
	background-color: rgba(0, 0, 0, .7);
	z-index: 100;

	&.off {
		@extend %none; }

	.loader {
		@extend %absolute;
		@extend %left_50;
		@extend %top_50;
		transform: translateX(-50%) translateY(-50%);
		width: 70px;
		height: 70px;
		line-height: 19px;
		& {
			.square {
				@extend %inline_block;
				@extend %bg-color_fff;
				@extend %opacity_1;
				width: 32px;
				height: 32px;
				animation: focusfade 1.2s infinite; } }
		& {
			.square:nth-child(2) {
				animation-delay: .3s; } }
		& {
			.square:nth-child(3) {
				animation-delay: .9s; } }
		& {
			.square:nth-child(4) {
				animation-delay: .6s; } } } }

@keyframes focusfade {
	0% {
		opacity: .6;
		background: #fff; }
	30% {
		opacity: .6;
		background: #000; }
	60% {
		opacity: .4; }
	75% {
		opacity: .6; }
	100% {
		opacity: .6;
		background: #fff; } }


